@include font(TT Trailers Black, TT_Trailers_900);
@include font(Mont Semi Bold, Mont_600);
@include font(Mont Bold, Mont_700);

@mixin Trailers {
	font-family: "TT Trailers Black", "Arial", sans-serif; }

@mixin MontSemiBold {
	font-family: "Mont Semi Bold", "Arial", sans-serif; }

@mixin MontBold {
	font-family: "Mont Bold", "Arial", sans-serif; }
