.container {
	max-width: 1750px;
	margin: 0 auto;
	padding: 0 100px;

	@include desktop-big {
		max-width: 1280px; }

	@include md {
		max-width: 1000px;
		padding: 0 20px; }

	@include smd {
		max-width: 100%; }

	@include sm {
		padding: 0 16px; }

	&_padding-max {
		padding: 0px 20px 0 240px;

		@include md {
			padding: 0 30px; } }

	&_padding-min {
		padding: 0 30px; }

	&_wider {
		max-width: 100%;
		padding: 0 30px;

		@include md {
			padding: 0 16px; }

		@include smd {
			max-width: 100%; } }

	&_padding {
		padding-top: 120px; }

	&_padding-small {
		padding-top: 72px; }

	&_banner {
		@include rmin(1280) {
			padding-top: 40px; } } }
