.sidebar {
	$this: &;

	z-index: 100;
	position: sticky;
	top: 75px;
	min-width: 195px;
	height: 100%;
	margin: 0 42px 0 -15px;
	padding-top: 5px;
	overflow-x: hidden;
	background: $black;

	@include md {
		margin: 0 35px 0 -15px; }

	@include smd {
		top: 70px;
		margin: 0 -16px; }

	&__list {
		display: block;
		margin-bottom: 5px;

		@include smd {
			display: flex;
			justify-content: space-between;
			margin: 0 -16px 0 0;
			padding-left: 16px;
			overflow-x: auto;

			&::-webkit-scrollbar {
 				visibility: hidden; } } }

	&__point {
		display: flex;
		height: 44px;
		white-space: nowrap;

		@include smd {
			height: 36px; }

		@include hover {
			#{$this}__button {
				color: $white;

				.icon {
					fill: $white; }

				.icon-fire {
					fill: $red; } } }

		&:focus {
			outline: none;

			#{$this}__button {
				border: 2px solid $golden;
				outline: none; } }

		@include nofocus {
			#{$this}__button {
				border: 2px solid transparent; } }

		&.is-active {
			#{$this}__button {
				color: $white;
				background: $dark-gray;
				pointer-events: none;

				.icon {
					fill: $white; }

				.icon-fire {
					fill: $red; } } }

		&_desktop {
			@include smd {
				order: -2; } }

		&_order {
			@include smd {
				order: -1; } }

		&_margin {
			margin-bottom: 13px; }

		&.is-disabled {
			#{$this}__button {
				pointer-events: none;
				color: $gray;

				.icon {
					fill: $gray; }

				.icon-fire {
					fill: $red;
					opacity: 0.4; } }

			@include hover {
				#{$this}__button {
					color: $gray;

					.icon {
						fill: $gray; } } } }

		&_padding {
			#{$this}__button {
				padding: 13px 16px 13px 35px;

				@include smd {
					padding: 9px 12px 9px 31px; } } } }

	&__button {
		position: relative;
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 100%;
		padding: 13px 16px;
		@include MontSemiBold;
		font-size: 16px;
		line-height: 1.3em;
		color: $light-gray;
		background: transparent;
		border-radius: 100px;
		border: 2px solid transparent;

		@include desktop-big {
			font-size: 14px; }

		@include smd {
			justify-content: center;
			padding: 9px 12px; }

		.icon {
			position: absolute;
			top: 50%;
			transform: translate(0, -50%);
			left: 16px;

			@include smd {
				left: 12px; }

			@include ios {
				top: calc(50% - 2px); } }

		.icon-mark {
			width: 11px;
			height: 13px;
			fill: $light-gray; }

		.icon-heart {
			width: 17px;
			height: 15px;
			fill: $light-gray;
			left: 14px;

			@include smd {
				left: 12px; } }


		.icon-clock {
			width: 14px;
			height: 14px;
			fill: $light-gray; }

		.icon-fire {
			width: 13px;
			height: 15px;
			fill: $red; } }

	&__name {
		display: flex;
		align-items: center;

		@include md {
			line-height: 1em; } }

	&__count {
		@include smd {
			display: none; } } }
