.course-about {
	margin-bottom: 100px;

	a {
		background: linear-gradient(to right,#535353 0%,#2c2c2c 100%) 0 100%/1px 1px repeat-x;
		color: #7f7f7f;

		@include hover {
			background: linear-gradient(to right,#fff 0%,#fff 100%) 0 100%/1px 1px repeat-x;
			color: #fff; } }

	p {
		max-width: 600px;
		width: 100%;
		letter-spacing: -0.2px;
		color: $courses-light-gray;
		font-size: 16px;
		margin-top: 25px;
		margin-bottom: 60px; }

	&__section {
		background: rgba(255, 255, 255, 0.05);
		display: flex;
		border-radius: 20px;
		padding: 30px 30px;

		@include sm {
			flex-direction: column; } }

	&__info {
		width: 290px;
		margin-right: 20px;
		padding-right: 30px;
		position: relative;
		padding-top: 20px;

		@include sm {
			width: 100%;
			margin-bottom: 50px;
			margin-right: 0;
			padding-right: 0; }

		img {
			width: 20px;
			position: absolute;
			top: -4px; }

		&:last-child {
			margin-right: 0;
			padding-right: 0;
			margin-bottom: 0; }

		&:last-child img {
			width: 16px;
			height: 21px;
			transform: scaleX(-1); } }

	&__title {
		color: rgba(255, 255, 255, 1);
		font-size: 33px;
		letter-spacing: -0.2px;
		display: block;
		margin: 12px 0 22px;
		line-height: 1; }

	&__desc {
		font-size: 14px;
		color: rgba(109, 117, 127, 1);
		letter-spacing: -0.2px;
		display: block;
		line-height: 18px; } }
