.popup {
	z-index: 2000;
	visibility: hidden;
	pointer-events: none;
	position: fixed;
	bottom: calc(50% - 80px);
	left: 50%;
	transform: translate(-50%, 50%);
	transition: bottom $transition, opacity $transition;
	opacity: 0;

	@include xs {
		bottom: -80px;
		width: 100%; }

	&.is-active {
		pointer-events: all;
		bottom: 50%;
		opacity: 1;

		@include xs {
			bottom: 0;
			transform: translate(-50%, 0); } }

	&__inner {
		position: relative;
		display: flex;
		flex-direction: column;
		align-items: center;
		width: 350px;
		padding: 56px 23px 23px;
		border-radius: 16px;
		background: $popup-grey;

		@include xs {
			width: 100%;
			border-radius: 16px 16px 0 0; } }

	&__inner--wide {
		width: 420px; }

	&__close {
		position: absolute;
		top: 16px;
		right: 16px;
		width: 24px;
		height: 24px;

		.icon {
			width: 15px;
			height: 15px;
			stroke: rgba(255, 255, 255, 0.3); } }

	&__logo {
		margin-bottom: 24px; }

	&__text {
		max-width: 100%;
		margin-bottom: 55px;
		@include MontBold;
		font-size: 24px;
		line-height: 1.3em;
		color: $white;
		text-align: center; }

	&__button {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: center;
		width: 302px;
		height: 48px;
		margin-bottom: 8px;
		padding: 15px 24px;
		@include MontSemiBold;
		font-size: 14px;
		color: $white;
		border-radius: 100px;

		@include xs {
			width: 100%; }

		&_blue {
			background: $blue;
			transition: background $anim;

			@include hover {
				background: $light-blue; }

			.icon {
				width: 8.5px;
				height: 16px;
				fill: $white; } }

		&_red {
			background: $dark-red;
			transition: background $anim;

			@include hover {
				background: $light-red; }

			.icon {
				width: 16px;
				height: 16px;
				fill: $white; } }

		.icon {
			position: absolute;
			left: 20px;
			top: 15px;

			@include xs {
				margin-right: auto; } } }

	&__checkbox-section {
		display: flex;
		align-items: center;
		margin-top: 10px; }

	&__label {
		line-height: 22px;
		color: $light-gray;
		font-size: 12px;
		font-weight: 100;
		cursor: pointer; }

	&__checkbox {
		position: relative;
		width: 20px;
		height: 20px;
		color: $black;
		border: 1px solid $label-checked;
		border-radius: 5px;
		appearance: none;
		outline: 0;
		cursor: pointer;
		transition: background 175ms cubic-bezier(0.1, 0.1, 0.25, 1);
		margin-right: 15px;

		&::before {
			position: absolute;
			content: '';
			display: block;
			top: 2px;
			left: 6px;
			width: 6px;
			height: 11px;
			border-style: solid;
			border-color: $label-border;
			border-width: 0 2px 2px 0;
			transform: rotate(45deg);
			opacity: 0; }

		&:checked {
			color: $white;
			border-color: $label-checked;
			background: $label-checked;

			&::before {
				opacity: 1; } }

		label {
			position: relative;
			cursor: pointer;
			font-size: 1.5em;
			font-weight: 600;
			padding: 0 0.25em 0;
			user-select: none;

			&::before {
				position: absolute;
				content: attr(data-content);
				color: $label-border;
				clip-path: polygon(0 0, 0 0, 0% 100%, 0 100%);
				text-decoration: line-through;
				text-decoration-thickness: 3px;
				text-decoration-color: $black;
				transition: clip-path 200ms cubic-bezier(0.25, 0.46, 0.45, 0.94); } } } }
