.title {
	@include Trailers;
	font-size: 80px;
	line-height: 1.1em;
	font-weight: 500;
	color: $white;
	letter-spacing: 0.005em;

	@include sm {
		font-size: 48px; }

	&_padding {
		@include smd {
			padding-bottom: 40px; }

		@include sm {
			padding-bottom: 80px; } }

	&_small-padding {
		@include smd {
			padding-bottom: 10px; }

		@include sm {
			padding-bottom: 20px; } }

	&_smaller {
		font-size: 64px;
		line-height: 1em;

		@include sm {
			font-size: 28px; }

		&_mob {
			@include sm {
				font-size: 32px; } } }

	&_smallest {
		font-size: 56px;

		@include desktop-big {
			font-size: 42px;
			line-height: 44px; }

		@include sm {
			font-size: 32px;
			line-height: 1.1em; } }

	&_tiny {
		font-size: 40px;
		line-height: 1.1em;

		@include sm {
			font-size: 28px;
			line-height: 1.7em; } }

	&_tiny-course {
		font-size: 60px;
		letter-spacing: -1px; } }

