.intro {
	$this: &;

	overflow-x: hidden;
	padding-top: 72px;

	&__gift-container {
		position: relative;
		left: 12px;
		top: 13px;

		@include hover {
			#{$this}__gift-text {
				color: white;
				border-bottom: 1px solid #fff; }

			#{$this}__gift-image {
				fill: black;
				stroke: #fff; } } }

	&__gift-text {
		font-size: 16px;
		@include MontSemiBold; }

	&__gift-image {
		stroke: #c39553;
		fill: #000;
		position: relative;
		top: 5px;
		margin-right: 7px; }

	&__container {
		position: relative;
		display: flex;
		align-items: center;
		min-height: 648px;
		height: 648px;

		@include md {
			flex-direction: column;
			align-items: flex-start;
			min-height: 830px;
			height: 830px; }

		@include sm {
			min-height: auto;
			height: auto; } }

	&__info {
		width: 583px;

		@include desktop-big {
			width: 368px; }

		@include md {
			width: 100%; } }

	&__title {
		margin-bottom: 24px;

		@include sm {
			margin-bottom: 32px; } }

	&__text {
		max-width: 344px;
		margin-bottom: 48px;
		line-height: 22px;
		color: $middle-gray;

		@include md {
			max-width: 100%; }

		@include sm {
			display: none; } }

	&__section {
		@include xs {
			margin-bottom: 15px; } }

	&__action {
		display: flex;
		align-items: center;

		@include md {
			justify-content: center; }

		@include xs {
			flex-direction: column-reverse; } }

	&__button {
		margin-right: 16px;

		@include xs {
			margin-right: 0; } }

	&__price {
		position: relative;
		display: flex;
		font-size: 16px;
		line-height: 1.4em;

		@include xs {
			flex-direction: row;
			justify-content: center;
			width: 100% {
    		margin-bottom: 20px; } } }

	&__currency {
		margin-right: 5px;


		&_symbol {
			display: block; } }


	&__image {
		position: absolute;
		bottom: 55px;
		right: 6%;
		width: 837px;

		@include desktop-big {
			right: -9%; }

		@include desktop-small {
			right: -4%;
			width: 770px; }

		@include md {
			order: -1;
			position: relative;
			bottom: 0;
			width: 760px;
			height: 530px;
			margin-bottom: 30px; }

		@include sm {
			right: -5%;
			top: 32px;
			width: 100%;
			height: 100%; }

		img {
			width: 100%;
			height: 100%;
			object-fit: contain; } } }
