.cards-block {
	&__list {
		display: flex;
		flex-wrap: wrap;
		max-width: 1286px;

		@include desktop-big {
			max-width: 860px; }

		@include sm {
			flex-direction: column;
			align-items: center;
			justify-content: center; } }

	&__item {
		margin: 0 10px 76px 0;

		@include sm {
			width: 100%;
			margin: 0 0 40px; }

		&_visibility {
			@include desktop-big {
				display: none; } } } }
