.course-cases-slider {
	// min-height: 670px
	margin-bottom: 70px;
	margin-top: 34px;

	&__heading {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-bottom: 30px; }

	&__button {
		width: 100%;
		padding: 15px 16px;
		background: #ffffff1c;
		color: #fff;
		border-radius: 40px;
		font-size: 15px;
		max-width: 183px;
		font-family: "Mont Semi Bold","Arial",sans-serif;
		margin-bottom: 13px;

		@include sm {
			display: none; } }

	&__button:hover {
		transition: .1s!important;
		background: #a00304; }

	&__title {
		font-family: 'TT Trailers Black','Arial',sans-serif;
		font-style: normal;
		text-decoration: none;
		text-transform: none;
		font-size: 60px;
		font-weight: 500;
		color: #fff;
		letter-spacing: -1.2px;
		line-height: 1.1em; }

	&__slider {
		position: relative;
		left: 0;
		max-width: 100vw;
		margin-right: calc(50% - 50vw + 180px);
		overflow-x: hidden;

		@include smd {
			margin-right: calc(50% - 50vw + 12px); }

		@include sm {
			margin-right: -15px; }

		&::after {
			z-index: 10;
			content: "";
			position: absolute;
			top: 0;
			right: -11px;
			width: 180px;
			height: 100%;
			background: linear-gradient(to right, rgba(16, 16, 20, 0) 0.35%, #101014 93.56%);

			@include sm {
				display: none; } } }

	&__inner {
		display: flex;
		align-content: stretch;
		overflow: auto;
		width: calc(100% + 20px);

		&::-webkit-scrollbar {
			visibility: hidden; } }

	&__socials img {
		width: 28px; }

	&__image-section {
		position: relative;
		width: 290px;
		height: 447px;
		margin-bottom: 15px;

		button {
			display: block; } }

	&__case-card {
		width: 290px;
		margin-right: 20px; }

	&__play-btn {
		width: 50px;
		position: absolute;
		bottom: 15px;
		left: 15px;
		cursor: pointer; }

	&__play-btn:hover {
		opacity: .7; }

	&__company-logo {
		width: 88px; }

	&__photo {
		border-radius: 20px;
		padding: 0;
		margin: 0;
		width: 100%;
		height: 100%;
		object-fit: cover; }

	&__position, &__name {
		display: block;
		letter-spacing: -.2px;
		color: $courses-light-gray;
		font-size: 14px;
		line-height: 1.4; }

	&__feedback {
		height: 130px; } }
