.card {
	$this: &;

	display: flex;
	flex-direction: column;
	align-items: flex-start;
	max-width: 311px;
	position: relative;

	@include desktop-big {
		max-width: 275px; }

	@include md {
		max-width: 230px; }

	@include sm {
		max-width: 100%; }

	&:focus {
		outline: 2px solid $golden; }

	@include nofocus {
		outline: none; }

	&_middle {
		max-width: 350px;

		@include sm {
			max-width: 100%; } }

	&_big {
		max-width: 418px;

		@include md {
			max-width: 350px; }

		@include sm {
			max-width: 100%; } }

	&_huge {
		flex-direction: row;
		max-width: 100%;
		margin-bottom: 62px;

		@include sm {
			flex-direction: column;
			margin-bottom: 40px; } }

	&__favorite {
		position: absolute;
		right: 0px;
		z-index: 15;
		top: 0px;
		display: none;
		width: 50px;
		height: 50px;
		justify-content: center;
		align-items: center;

		@include hover {
			opacity: .4; }

		.icon-heart {
			width: 25px;
			height: 21px;
			fill: #fff; }

		.icon-heart-outline {
			width: 25px;
			height: 21px;
			fill: #fff; } }

	&__link {
		max-width: 100%;
		height: 100%;

		@include sm {
			width: 100%; }

		@include hover {
			.card__favorite {
				display: flex; }

			#{$this}__text {
				color: $golden; }

			#{$this}__text {
				color: $golden; }

			#{$this}__photo {
				transform: scale(1.06); }

			&+#{$this}__inner {
				#{$this}__text {
					color: $golden; } } } }

	&__image {
		position: relative;
		overflow: hidden;
		width: 311px;
		height: 192px;
		margin-bottom: 18px;

		@include desktop-big {
			width: 275px;
			height: 171px; }

		@include md {
			width: 230px;
			height: auto; }

		@include sm {
			width: 100%;
			max-width: 675px; }

		&_middle {
			width: 350px;
			height: 217px;

			@include sm {
				width: 100%;
				height: auto; } }

		&_big {
			width: 418px;
			height: 260px;

			@include md {
				width: 350px;
				height: auto; }

			@include sm {
				width: 100%; } }

		&_huge {
			width: 624px;
			height: 380px;
			margin-right: 24px;

			@include md {
				width: 480px;
				height: auto; }

			@include sm {
				width: 100%; } }

		&_vertical {
			width: 278px;
			height: 400px; }

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			transition: transform 0.7s cubic-bezier(0.16, 1, 0.3, 1); } }

	&__progress-bar {
		z-index: 10;
		display: none;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 4px;

		&.is-active {
			display: block; } }

	&__progress {
		width: 50%;
		height: 4px;
		background: $golden; }

	&__label {
		display: none;
		position: absolute;
		left: -2px;
		bottom: 27px;
		min-width: 66px;
		min-height: 20.1px;
		padding: 0 3px;
		background: $golden;

		&::after {
			content: "";
			position: absolute;
			top: -8px;
			right: 0;
			width: 100%;
			min-height: 9px;
			border: 11px solid transparent;
			border-right: 100px solid $golden;
			border-bottom: 0px solid $golden; }

		&::before {
			content: "";
			position: absolute;
			top: 19px;
			left: -35px;
			width: 100%;
			min-height: 9px;
			border: 11px solid transparent;
			border-right: 100px solid $golden;
			border-bottom: 0px solid $golden;
			transform: rotate(180deg); }

		&.is-active {
			display: flex;
			justify-content: center;
			align-items: center; } }

	&__label-text {
		z-index: 20;
		@include MontBold;
		font-size: 12px;
		line-height: 1em;
		color: $white;
		text-transform: uppercase;
		letter-spacing: 2pt;
		transform: rotate(-6deg); }

	&__info {
		display: flex;
		flex-wrap: wrap;
		margin-bottom: 10px;
		font-size: 13px;
		line-height: 1.3em;
		color: $light-gray;

		@include desktop-big {
			font-size: 12px; } }

	&__year {
		margin-right: 16px; }

	&__category {
		margin-right: 8px; }

	&__text {
		margin-bottom: 10px;
		font-size: 16px;
		line-height: 1.5em;
		color: $white;

		@include desktop-big {
			font-size: 14px; }

		&_big {
			font-size: 18px;

			@include desktop-big {
				font-size: 16px; }

			@include md {
				font-size: 14px; } }

		.icon {
			width: 13px;
			height: 16px;
			margin-bottom: -3px;
			fill: $red; } }

	&__speaker {
		max-width: 100%;
		padding-bottom: 4px;
		font-size: 13px;
		line-height: 1.3em;

		@include desktop-big {
			font-size: 12px; }

		&_absolute {
			position: absolute;
			bottom: 0;
			left: 0; }

		&.link {
			border-bottom: 0;

			@include hover {
				border-bottom: 0; } }

		span {
			border-bottom: 1px solid $border-grey; }

		@include hover {
			span {
				color: $white;
				border-bottom: 1px solid $white; } } }

	&__inner {
		position: relative;
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		max-width: 100%; }

	&__duration {
		.icon {
			width: 10px;
			height: 10px;
			fill: $light-gray; } } }
