.course-button {
	$this: &;

	position: relative;
	width: 100%;
	padding: 15px 16px;
	background: #ffffff1c;
	color: #fff;
	border-radius: 40px;
	font-size: 15px;
	max-width: 183px;
	appearance: none;
	@include MontSemiBold;
	margin-bottom: 13px;

	@include hover {
		transition: .1s!important;
		background: #a00304; }

	&_big {
		appearance: none;
		font-family: inherit;
		font-weight: 500;
		font-size: 18px;
		margin-top: -6px;
		appearance: none;
		border-radius: 56px {
	    padding: 20px 46px; } }

	&_bordered {
		background-color: transparent;
		border-radius: 40px;
		border: 3px solid #957236;
		color: #fff;
		padding: 12px 13px;
		font-weight: 500;
		font-size: 15px;
		letter-spacing: 0; }

