.course-intro {
	padding-top: 72px;

	.course-section {
		padding: 0 0px 0 140px;

		@include sm {
			padding: 0; } }

	&__wrapper {
		display: flex;
		justify-content: space-between;

		@include sm {
			flex-direction: column; } }

	&__back-button {
		width: 62px;
		height: 62px;
		left: -5px;
		position: relative;

		@include hover {
			opacity: .8; }

		img {
			width: 17px;
			height: 31px;
			transform: scaleX(-1); } }

	&__info {
		width: 660px;

		@include sm {
			width: 100%; } }

	&__support {
		display: flex;
		flex-direction: column;
		align-content: start;
		justify-content: center;
		padding-top: 143px;
		padding-right: 53px;

		@include sm {
			width: 100%;
			margin-top: 50px;
			padding: 0; }

		span {
			text-transform: uppercase;
			letter-spacing: 1.6px;
			font-size: 13px;
			color: $courses-brown;
			display: block; }

		img {
			width: 152px;
			height: 36px;
			opacity: 1;
			position: relative;
			top: 8px;
			left: -3px; } }

	&__breadcrumb {
		display: block;
		margin-top: 21px;

		a {
			text-decoration: none;
			padding-bottom: 3px;
			background: linear-gradient(to right, #2c2c2c 0%, #2c2c2c 100%) 0px 100% / 1px 1px repeat-x;
			color: #6b727c;
			font-size: 16px;
			letter-spacing: -0.4px; }

		a:hover {
			color: white;
			background: linear-gradient(to right, white 0%, white 100%) 0px 100% / 1px 1px repeat-x; } }

	&__register-btn {
		border: 3px solid $courses-second-brown;
		font-weight: 500;
		font-style: normal;
		color: #ffffff;
		font-size: 15px;
		letter-spacing: 0px;
		border-radius: 35px;
		padding: 14px 48px;
		margin-top: 42px;
		border-radius: 35px;
		padding: 12px 44px;
		margin-top: 39px;
		font-family: "Mont Semi Bold","Arial",sans-serif;
		transition: 0.5s !important; }

	&__register-btn:hover {
		transition: 0.5s !important;
		border: 3px solid $courses-white; }

	&__title {
		font-size: 85px;
		font-family: 'TT Trailers Black', 'Arial', sans-serif;
		line-height: 1.0em;
		font-weight: 500;
		color: #e8e8f4;
		margin-top: 22px;
		display: block;
		letter-spacing: 0;

		@include xs {
			font-size: 62px; }

		@include xxs {
			font-size: 62px; } }

	&__desc {
		letter-spacing: -.2px;
		color: $courses-light-gray;
		font-size: 16px;
		margin: 24px 0 10px;
		line-height: 22px;
		padding-right: 20px; }

