.search-results-empty {
	display: flex;
	flex-direction: column;
	align-items: center;
	height: 100%;
	padding-top: 97px;

	@include smd {
		padding-top: 85px; }

	&__image {
		width: 109px;
		height: 24px;
		margin-bottom: 24px; }

	&__text {
		max-width: 221px;
		margin-bottom: 24px;
		font-weight: 500;
		color: $light-gray;
		text-align: center; }

	&__link {
		@include MontSemiBold;
		font-size: 14px; } }
